import React, { Component } from "react";
import DynamicForm from "../components/DynamicForm";
import { servicesVersion } from "typescript";
import AnimalFoodService from './../service/AnimalFoodService';
import { numberWithCommas } from "../service/Models";

export default class AnimalFoodPage extends Component {
   animalFoodService=new AnimalFoodService();
 
   constructor(props) {
    super(props);
    this.state = { };

  }

  getConfig() {
    let config = {
      formClass: "form-row",
      buttonName: "",
      buttonId: "Save",
      buttonCallback: (form, history) => {
        //  history.push('/InventoryStatus')

        this.save();
      },
      buttonClass: "btn btn-primary",
      fields: [
        {
          name: "Name",
          placeholder: " ",
          title: " ",
          type: "text",
          required: true,
          col: "col-md-6",
        },
        {
          name: "Remain",
          placeholder: " ",
          title: " ",
          type: "number",
          required: true,
          col: "col-md-6",
        },
        {
          name: "PerUnitPrice",
          placeholder: "   ()",
          title: "   ()",
          type: "money",
          required: true,
          col: "col-md-6",
        },
        {
          name: "PerCustomerTotalMalQouta",
          placeholder: "     ",
          title: "     ",
          type: "number",
          required: true,
          col: "col-md-6",
        },
        
        {
          name: "Image",
          placeholder: "",
          title: "",
          type: "file",
          required: true,
          col: "col-md-6",
        },
      ],
    };
    return config;
  }

  save(){
    let form=this.childDynamicFormComponentReference.getState().form;

    if(!form.Name){
      alert('   ');
      return;
    }
    if(form.Remain==null){
      form.Remain=0;
    }
    
    if( !this.state.editing && form.Remain==null){
      alert('   ');
      return;
    }

    this.childDynamicFormComponentReference.resetForm();


    this.animalFoodService.save(form).then(res=>{

      if(res.Status===1){

        this.readAllFoods();
      }else{
        alert(res.Message);
      }


    });

  }

  componentWillMount(){
    this.readAllFoods();
  }


  readAllFoods(){

    return this.animalFoodService.getAll().then(res=>{
      if(res.Status==1){
        this.setState({animalFoods:res.EntityList});
      }else{
        alert(res.Message);
      }
    });

  }
  childDynamicFormComponentReference;

  render() {
    let config = this.getConfig();
    return (
      <div className="row">
        <div className="col-md-12" data-testid="addForm">
          <DynamicForm
            config={config}
            setParent={this}
            setParentName="childDynamicFormComponentReference"
          />
        </div>

        <div className="col-md-12">
          <table class="table" data-testid="table">
            <thead>
              <tr>
                <th scope="col">#</th>
                <th scope="col"></th>
                <th scope="col"></th>
                <th scope="col">     </th>
                <th scope="col"> </th>
                
                <th scope="col"></th>
              </tr>
            </thead>
            <tbody data-testid="rows">{this.showRows()}</tbody>
          </table>
        </div>
      </div>
    );
  }
  showRows() {
    if (!this.state.animalFoods) {
      return <></>;
    }

    return this.state.animalFoods.map((val, i, arr) => {
      return (
        <tr key={val.Id}>
          <th scope="row">{val.Id}</th>
          <td>{val.Name}</td>
          <td>{val.Remain}</td>
          <td>{val.PerCustomerTotalMalQouta}</td>
          
          <td style={{direction:'rtl'}}>  {numberWithCommas(val.PerUnitPrice)}   </td>
          <td><button className="btm btn-info" 
           onClick={()=>this.edit(val)}></button></td>
        </tr>
      );
    });
  }


  edit(val){

    
    this.childDynamicFormComponentReference.setState({form:val});
  }
}
